import React, {useState} from 'react'


export default function Mytext(props) {
    const [text, setText] = useState("");
    
    
    const handleUpclick = () =>{
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("Converted to UpperCase", "success");
    }

    const handleloclick = () =>{
        let newText = text.toLocaleLowerCase();
        setText(newText)
        props.showAlert("Converted to LowerCase", "success");
    }

    const handlecleartext = () =>{
        let newText = '';
        setText(newText)
        props.showAlert("Text Cleared", "success");
    }

    const handlecopytext = () =>{
        var text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
        props.showAlert("Copied", "success");
        

    }

    const handleCapitext = () =>{
       let a = text.split(" ");
       for (var i =0; i<a.length; i++){
        a[i] = a[i].charAt(0).toUpperCase() + a[i].slice(1);
       }
       let b = a.join(" ");
       setText(b)
       props.showAlert("First Letter has been capitalized", "success");
        
    }
    
    const handleOnchange = (event)=>{
        setText(event.target.value);
    }

  return (
    <>

    <div className='container' style={{color: props.mode==='dark'?'white':'black'}}>
        <h1>{props.heading}</h1>
        <div className="mb-3">
            <textarea className="form-control" value={text} onChange={handleOnchange} style={{backgroundColor: props.mode==='dark'?'grey':'white',color: props.mode==='dark'?'white':'black'}} id="myBox" rows="8"></textarea>
        </div>
            <button className='btn btn-primary mx-2' onClick={handleUpclick}>Convert to Uppercase</button>
            <button className='btn btn-primary mx-2' onClick={handleloclick}>Convert to Lowercase</button>
            <button className='btn btn-primary mx-2' onClick={handlecleartext}>Clear Text</button>
            <button className='btn btn-primary mx-2' onClick={handlecopytext}>Copy Text</button>
            <button className='btn btn-primary mx-2' onClick={handleCapitext}>Capitalize First Letter</button>
    </div>
    <div className="container my-3" style={{color: props.mode==='dark'?'white':'black'}}>
        <h2>Your Text Summary</h2>
        <p>{text.split(" ").length} Words and {text.length} Characters</p>
        <p>{0.008 * text.split(" ").length} Minutes Read</p>
        <h2>Preview</h2>
        <p>{text.length>0?text:"Enter above in textbox to preview here"}</p>
        
    </div>
    </>
  )
}
